<template>
    <div>
        <el-form ref="form" :model="form" label-width="130px">
            <el-row class="pd20 bgc-fff">
                <el-tabs v-model="tabsActive" @tab-click="handleTabsClick">
                    <el-tab-pane label="助贷消息" name="1"></el-tab-pane>
                    <el-tab-pane label="自贷消息" name="2"></el-tab-pane>
                </el-tabs>
                <el-card class="box-card" shadow="hover">
                    <div v-for="item in dataList" :key="item.create_time" class="pb20 c5a5a5a">
                        {{ `用户${item.borrower}，身份证号：${item.id_card}，联系电话：${item.contact_way}，${item.create_time} 申请贷款 ${item.total_loan_amount}请知悉。` }}
                    </div>
                </el-card>

                <div class="pagination">
                    <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
                    </el-pagination>
                </div>
            </el-row>
            
            <el-form-item class="form-submit">
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import {creditManagementMessageList} from '@/api/creditManagement'
    export default {
        name: 'baseform',
        data: function(){
            return {
                dataList: [],
                page: 1,
                total: 1,
                limit: 10,
                tabsActive: '1',
                pageType: 'add',
                form: {
                    name: '阿萨德'
                }
            }
        },
        mounted(){
            this.getData()
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData(){
                creditManagementMessageList({credit_type: this.tabsActive, page: this.page}).then(res => {
                    this.dataList=[]
                    Object.assign(this.dataList,res.data)
                })
            },
            handleTabsClick(val){
                this.getData();
            }
        }
    }
</script>

